(function() {
  'use strict';

  angular
    .module('public')
    .directive('ngSenseItem', ngSenseItem);

  /** @ngInject */
  function ngSenseItem() {
    var directive = {
      restrict: 'A',
      replace: true,
      template: function (elem, attrs) {
        if (attrs.gridView == "true") {
            return '<div ng-include src="dynamicTemplateUrl"></div>';
        }
        else {
            return '<tbody ng-include src="dynamicTemplateUrl"></tbody>';
        }
      },
      scope: {
          senseId: '@',
          imgId: '@',
          deviceStatus: '='
      },
      link: function(scope, element, attrs) {
        scope.dynamicTemplateUrl = 'app/components/senseItem/senseItem-list.html';
        scope.sense = {};

        attrs.$observe('senseId', function(senseId) {
          scope.sense.senseId = senseId;
          scope.getModules();
          scope.getSenseStatus();

        });

        attrs.$observe('imgId', function(imgId) {
          scope.sense.imgId = imgId;
        });

        attrs.$observe('gridView', function(gridView) {
          scope.gridView = gridView;
          if (scope.gridView == "true") {
              scope.dynamicTemplateUrl = 'app/components/senseItem/senseItem-grid.html';
          }
          else {
              scope.dynamicTemplateUrl = 'app/components/senseItem/senseItem-list.html';
          }
        });
      },
      controller: senseItemController,
      controllerAs: 'senseItemController',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function senseItemController(deviceList, status, moduleList, $interval, $scope) {
      $scope.logButtonText = 'View';
      $scope.expandButtonText = '+';
      $scope.showModules = false;
      $scope.showLogViewer = false;

      $interval(getSenseStatus, 10000);

      function getSenseStatus() {
        status.SenseStatus.get({senseId: $scope.sense.senseId}, function(response) {
          $scope.sense.deviceStatus = response.status;
        });
      }

      $scope.getSenseStatus = getSenseStatus;

      $scope.getModules = function () {
        moduleList.Modules.get({senseId: $scope.sense.senseId}, function(response) {
          $scope.modules = angular.fromJson(response.result);
          $scope.moduleCount = $scope.modules.length;
        });
      };

      $scope.displayModules = function () {
          if ($scope.expandButtonText == '+') {
            $scope.expandButtonText = '-';
          } else if ($scope.expandButtonText == '-') {
            $scope.expandButtonText = '+';
          }
          $scope.showModules = !$scope.showModules;
      };

      $scope.displayLog = function () {
          if ($scope.logButtonText == 'View') {
            $scope.logButtonText = 'Hide';
          } else {
            $scope.logButtonText = 'View';
          }
          $scope.showLogViewer = !$scope.showLogViewer;
      };

      $scope.deleteSense = function(senseId) {
        if (confirm("Do you want to delete " + senseId + " ?")) {
          deviceList.delDevice(senseId);
        }
      }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('public')
    .directive('ngSenseContent', senseContent);

  /** @ngInject */
  function senseContent() {
    var directive = {
      restrict: 'E',
      template: '<ng-include src="dynamicTemplateUrl"></ng-include>',
      //templateUrl: 'app/components/senseContent/senseContent-table.html',
      scope: {
          creationDate: '='
      },
      link: function(scope, element, attrs) {
        scope.dynamicTemplateUrl = 'app/components/senseContent/senseContent-list.html';
        attrs.$observe('viewType', function(viewType) {
          if (viewType == "grid") {
              scope.gridView = "true";
          }
          else {
              scope.gridView = "false";
          }
        });

        scope.$watch('gridView', function(gridView) {
          scope.gridView = gridView;
          if (scope.gridView == "true") {
              scope.dynamicTemplateUrl = 'app/components/senseContent/senseContent-grid.html';
          }
          else {
              scope.dynamicTemplateUrl = 'app/components/senseContent/senseContent-list.html';
          }
        });
      },
      controller: senseContentController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function senseContentController(deviceList, $scope, $modal, $log) {
      var vm = this;

      vm.getDevices = deviceList.getDevices;
      vm.delDevice = deviceList.delDevice;
      // vm.deviceEmpty = (angular.equals(vm.getDevices(),{})) ? true: false;

      // $scope.$watch(function(){ return deviceList.getDevices}, function(newVal, oldVal){
      //   $log.info('data change'+newVal+' '+oldVal);
      // }, true);

      vm.viewButtonClick = function() {
          $log.info('org scope.gridView: ' + $scope.gridView);
          $scope.gridView = !$scope.gridView;
      };

      vm.open = function(){

        var modalInstance = $modal.open({
          animation: 1,
          templateUrl: 'addModalContent.html',
          controller: modalController,
          controllerAs: 'vm'
        });

        modalInstance.result.then(function (sense) {

          deviceList.setDevice(sense.id, sense.imgId);

          $log.info('Generating sense id: ' + sense.id + ', imageId:' + sense.imgId);

        }, function () {

          $log.info('Modal dismissed at: ' + new Date());

        });
      };
    }

    function modalController($modalInstance){
      var vm = this;

      vm.imgId = 1 + Math.floor(Math.random() * 9); // random image id

      vm.senseId = ""; // sense id


      vm.ok = function(){
        $modalInstance.close({id: vm.senseId , imgId: vm.imgId});
      };

      vm.cancel = function(){
        $modalInstance.dismiss('cancel');
      };
    }
  }

})();

